import {Component, ChangeDetectionStrategy, ChangeDetectorRef, Inject, Optional, HostBinding, ElementRef} from '@angular/core';
import {BreakpointObserver} from '@angular/cdk/layout';
import {GridPluginInstances, GRID_PLUGIN_INSTANCES, HeaderContentRendererAbstractComponent, HEADER_CONTENT_RENDERER_OPTIONS} from '@anglr/grid';
import {extend} from '@jscrpt/common';
import {Subscription} from 'rxjs';

import {ResponsiveCssDivsHeaderContentRendererOptions} from '../responsiveCssDivsContentRenderer.interface';
import {RESPONSIVE_WIDTH_MEDIA} from '../../../../../../misc/constants';

/**
 * Default options for 'ResponsiveCssDivsHeaderContentRendererComponent'
 * @internal
 */
const defaultOptions: ResponsiveCssDivsHeaderContentRendererOptions =
{
    cssClasses:
    {
        headerDiv: 'header-row-contents',
        headerCellDiv: 'header-cell',
        headerCellOrderableDiv: 'header-orderable',
        spanContent: 'header-content',
        spanOrdering: 'header-ordering',
        spanOrderingDirection:
        {
            none: 'fa fa-sort',
            asc: 'fa fa-sort-up',
            desc: 'fa fa-sort-down'
        }
    }
};

/**
 * Component used for rendering css grid header in css grid content renderer
 */
@Component(
{
    selector: 'div.content-renderer-header',
    templateUrl: 'responsiveCssDivsHeaderContentRenderer.component.html',
    styleUrls: ['responsiveCssDivsHeaderContentRenderer.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResponsiveCssDivsHeaderContentRendererComponent<TData = any> extends HeaderContentRendererAbstractComponent<TData, ResponsiveCssDivsHeaderContentRendererOptions>
{
    //######################### protected fields #########################

    /**
     * Subscription for responsive changes
     */
     protected _responsiveSubscription: Subscription;

    //######################### public properties - hosts #########################

    /**
     * Css class applied to grid itself
     */
    @HostBinding('class')
    public get cssClass(): string
    {
        return this._options.cssClasses.headerDiv;
    }

    //######################### public properties - template binding #########################

    /**
     * Indication whether is responsive view enabled
     */
     public responsiveView: boolean = false;

    //######################### constructor #########################
    constructor(protected _breakPointObserver: BreakpointObserver,
                pluginElement: ElementRef,
                changeDetector: ChangeDetectorRef,
                @Inject(HEADER_CONTENT_RENDERER_OPTIONS) @Optional() options: ResponsiveCssDivsHeaderContentRendererOptions,
                @Inject(GRID_PLUGIN_INSTANCES) @Optional() gridPlugins: GridPluginInstances)
    {
        super(pluginElement, gridPlugins, changeDetector);

        this._options = extend(true, {}, defaultOptions, options);
    }

    //######################### public methods - implementation of OnInit #########################
    
    /**
     * Initialize component
     */
     public ngOnInit()
     {
         this._responsiveSubscription = this._breakPointObserver.observe(RESPONSIVE_WIDTH_MEDIA)
             .subscribe(({matches}) =>
             {
                 this.responsiveView = matches;
                 this._changeDetector.detectChanges();
             });
     }
 
     //######################### public methods - implementation of OnDestroy #########################
     
     /**
      * Called when component is destroyed
      */
     public ngOnDestroy()
     {
         this._responsiveSubscription?.unsubscribe();
         this._responsiveSubscription = null;
     }
}
