import {Directive, TemplateRef, Inject, ViewContainerRef, Input, OnChanges, SimpleChanges, OnInit} from '@angular/core';
import {nameof, isBlank} from '@jscrpt/common';

import {IS_ELECTRON} from '../../misc';

/**
 * Directive that displays element only when application is running in electron
 */
@Directive(
{
    selector: '[ifElectron]'
})
export class IfElectronDirective implements OnChanges, OnInit
{
    //######################### public properties - inputs #########################

    /**
     * Additional condition passed to directive
     */
    @Input('ifElectron')
    public additionalCondition: boolean|string;

    //######################### constructor #########################
    constructor(private _template: TemplateRef<void>,
                @Inject(IS_ELECTRON) private _isElectron: boolean,
                private _viewContainer: ViewContainerRef)
    {
    }

    //######################### public methods - implementation of OnInit #########################
    
    /**
     * Initialize component
     */
    public ngOnInit()
    {
        if(this._isElectron && isBlank(this.additionalCondition))
        {
            const view = this._viewContainer.createEmbeddedView(this._template);
            this._viewContainer.insert(view);
        }
    }

    //######################### public methods - implementation of OnChanges #########################
    
    /**
     * Called when input value changes
     */
    public ngOnChanges(changes: SimpleChanges): void
    {
        if(nameof<IfElectronDirective>('additionalCondition') in changes)
        {
            if(this._isElectron && this.additionalCondition)
            {
                const view = this._viewContainer.createEmbeddedView(this._template);
                this._viewContainer.insert(view);
            }
            else
            {
                this._viewContainer.clear();
            }
        }
    }
}