import {InjectionToken} from '@angular/core';

import {AppStable} from '../services/isStable';
import {TitleBar} from '../services/titleBar';
import {SettingsStorage} from '../services/settings';
import {FileSink} from '../services/logger/logger.interface';

/**
 * Token used for retrieving instance of class that is used for indicating that app is stable
 */
export const NG_APP_STABLE: InjectionToken<AppStable> = new InjectionToken<AppStable>('NG_APP_STABLE');

/**
 * Token used for handling title bar operations
 */
export const TITLE_BAR: InjectionToken<TitleBar> = new InjectionToken<TitleBar>('TITLE_BAR');

/**
 * Token used for settings storage
 */
export const SETTINGS_STORAGE: InjectionToken<SettingsStorage> = new InjectionToken<SettingsStorage>('SETTINGS_STORAGE');

/**
 * Token used for obtaining file logger
 */
export const FILE_LOGGER: InjectionToken<FileSink> = new InjectionToken<FileSink>('FILE_LOGGER');