import {Injectable, Optional, Inject, Injector, Type} from '@angular/core';
import {Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import {HTTP_REQUEST_BASE_URL, PermanentStorage, PERMANENT_STORAGE} from '@anglr/common';
import {RESTClient, BaseUrl, DefaultHeaders, REST_MIDDLEWARES_ORDER, REST_METHOD_MIDDLEWARES, RestMiddleware} from '@anglr/rest';
import {AuthenticationServiceOptions, UserIdentity} from '@anglr/authentication';
import {KeycloakService} from 'keycloak-angular';
import {EMPTY, NEVER, Observable, Observer} from 'rxjs';

import {config} from '../../../config';
import {IS_ELECTRON} from '../../../modules';
import {AUTH_TOKEN} from '../../../misc/constants';

/**
 * Service used to access user account information
 */
@Injectable()
@BaseUrl(config.configuration.apiBaseUrl)
@DefaultHeaders(config.configuration.defaultApiHeaders)
export class AccountService extends RESTClient implements AuthenticationServiceOptions<any>
{
    //######################### constructor #########################
    constructor(http: HttpClient,
                injector: Injector,
                private _keycloakSvc: KeycloakService,
                @Inject(PERMANENT_STORAGE) private _storage: PermanentStorage,
                @Optional() @Inject(HTTP_REQUEST_BASE_URL) baseUrl?: string,
                @Inject(REST_MIDDLEWARES_ORDER) middlewaresOrder?: Type<RestMiddleware>[],
                @Inject(REST_METHOD_MIDDLEWARES) methodMiddlewares?: Type<RestMiddleware>[])
    {
        super(http, baseUrl, injector, middlewaresOrder, methodMiddlewares);
    }

    //######################### public methods - implementation of AuthenticationServiceOptions #########################

    /**
     * Method logs user into system
     * @returns Observable
     */
    public login(): Observable<any>
    {
        return EMPTY;
    }

    /**
     * Gets indication whether current state of app is displaying login page
     * @returns boolean
     */
    public isAuthPage(): boolean
    {
        return false;
    }

    /**
     * Methods logs out user out of system
     * @returns Observable
     */
    public logout(): Observable<any>
    {
        this._keycloakSvc.clearToken();

        this._keycloakSvc
            .logout(config.configuration.keycloak.redirectUri)
            .then(() => 
            {
                this._storage.remove(AUTH_TOKEN);
                window.location.href = '/';
            })
            .catch(() => 
            {
                this._storage.remove(AUTH_TOKEN);
                // window.location.reload();
                window.location.href = '/';
            });

        return NEVER;
    }

    /**
     * Gets information about user
     * @returns Observable
     */
    public getUserIdentity(): Observable<UserIdentity<any>>
    {
        const isElectron = this.injector.get(IS_ELECTRON);

        return Observable.create(async (observer: Observer<any>) =>
        {
            //authenticated
            if(await this._keycloakSvc.isLoggedIn())
            {
                const profile = await this._keycloakSvc.loadUserProfile();
                let privileges = this._keycloakSvc.getUserRoles();
                // const token: string = await this._keycloakSvc.getToken();
                // const tokenPayload = this._getTokenPayload(token);

                privileges = privileges.concat(isElectron ? ['authorized-electron'] : ['authorized-browser']);

                observer.next(
                {
                    isAuthenticated: true,
                    userName: profile.username,
                    firstName: profile.firstName,
                    surname: profile.lastName,
                    permissions: privileges.concat(['authenticated']),
                    //additionalInfo: { omType: tokenPayload.omType },
                });

                observer.complete();
            }
            else
            {
                observer.next(
                {
                    isAuthenticated: false,
                    userName: '',
                    permissions: [].concat(isElectron ? ['authorized-electron'] : ['authorized-browser']),
                    firstName: '',
                    surname: ''
                });
                
                observer.complete();
            }
        });
    }

    /**
     * Redirects current page to authentication page
     */
    public showAuthPage(): Promise<boolean>
    {
        return this._keycloakSvc.login() as any;
    }

    /**
     * Redirects current page to access denied page
     */
    public showAccessDenied(): Promise<boolean>
    {
        return this.injector.get(Router).navigate(['/accessDenied']);
    }
}