import {Pipe, PipeTransform} from '@angular/core';
import {BasicTableColumn} from '@anglr/grid';

/**
 * Pipe used for obtaining grid template columns
 */
@Pipe({name: 'gridTemplateColumns'})
export class GridTemplateColumnsPipe implements PipeTransform
{
    //######################### public methods - implementation of PipeTransform #########################

    /**
     * Transforms array of columns to grid template columns css style
     * @param columns Array of columns to be displayed
     * @param responsiveView Indication whether is responsive view displayed
     * @param responsiveStyleValue Css style value that will be used for responsive view
     */
    public transform(columns: BasicTableColumn[],
                     responsiveView: boolean = false,
                     responsiveStyleValue: string = 'auto auto'): string
    {
        if(responsiveView)
        {
            return responsiveStyleValue;
        }

        if(!columns?.length)
        {
            return '';
        }
        else
        {
            const gridTemplateColumns: string[] = [];

            columns.forEach(column =>
            {
                if (column.visible)
                {
                    gridTemplateColumns.push(column.width || 'auto');
                }
            });

            return gridTemplateColumns.join(' ');
        }
    }
}