import {Component, ChangeDetectionStrategy, ChangeDetectorRef, Optional, Inject, HostBinding, ElementRef, OnInit, OnDestroy} from '@angular/core';
import {BreakpointObserver} from '@angular/cdk/layout';
import {BasicTableColumn, BasicTableMetadata, BodyContentRendererAbstractComponent, BODY_CONTENT_RENDERER_OPTIONS, GridPluginInstances, GRID_PLUGIN_INSTANCES} from '@anglr/grid';
import {extend} from '@jscrpt/common';
import {Subscription} from 'rxjs';

import {CssClassesResponsiveCssDivsBodyContentRenderer, ResponsiveCssDivsBodyContentRendererOptions} from '../responsiveCssDivsContentRenderer.interface';
import {RESPONSIVE_WIDTH_MEDIA} from '../../../../../../misc/constants';

/**
 * Default options for 'ResponsiveCssDivsBodyContentRendererComponent'
 * @internal
 */
const defaultOptions: ResponsiveCssDivsBodyContentRendererOptions =
{
    cssClasses:
    {
        bodyDiv: 'body-div-contents',
        rowDiv: 'body-row-contents',
        responsiveRowDiv: 'body-responsive-row-contents',
        responsiveRowTitleColumn: 'body-responsive-row-title',
        cellDiv: 'body-cell'
    }
};

/**
 * Component used for rendering body for 'ResponsiveCssDivsContentRenderer'
 */
@Component(
{
    selector: 'div.content-renderer-body',
    templateUrl: 'responsiveCssDivsBodyContentRenderer.component.html',
    styleUrls: ['responsiveCssDivsBodyContentRenderer.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResponsiveCssDivsBodyContentRendererComponent<TData = any> extends BodyContentRendererAbstractComponent<TData, ResponsiveCssDivsBodyContentRendererOptions, BasicTableMetadata<BasicTableColumn<TData>>, CssClassesResponsiveCssDivsBodyContentRenderer> implements OnInit, OnDestroy
{
    //######################### protected fields #########################

    /**
     * Subscription for responsive changes
     */
    protected _responsiveSubscription: Subscription;

    //######################### public properties - host bindings #########################

    /**
     * Css class applied to grid itself
     */
    @HostBinding('class')
    public get cssClass(): string
    {
        return this._options.cssClasses.bodyDiv;
    }

    //######################### public properties - template binding #########################

    /**
     * Indication whether is responsive view enabled
     */
    public responsiveView: boolean = false;

    //######################### constructor #########################
    constructor(protected _breakPointObserver: BreakpointObserver,
                pluginElement: ElementRef,
                changeDetector: ChangeDetectorRef,
                @Inject(GRID_PLUGIN_INSTANCES) @Optional() gridPlugins: GridPluginInstances,
                @Inject(BODY_CONTENT_RENDERER_OPTIONS) @Optional() options: ResponsiveCssDivsBodyContentRendererOptions)
    {
        super(pluginElement, changeDetector, gridPlugins);

        this._options = extend(true, {}, defaultOptions, options);
    }

    //######################### public methods - implementation of OnInit #########################
    
    /**
     * Initialize component
     */
    public ngOnInit()
    {
        this._responsiveSubscription = this._breakPointObserver.observe(RESPONSIVE_WIDTH_MEDIA)
            .subscribe(({matches}) =>
            {
                this.responsiveView = matches;
                this._changeDetector.detectChanges();
            });
    }

    //######################### public methods - implementation of OnDestroy #########################
    
    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        this._responsiveSubscription?.unsubscribe();
        this._responsiveSubscription = null;
    }
}
