import {Type} from '@angular/core';
import {PreloadAllModules} from '@angular/router';
import {ModuleRoutesOptions} from '@anglr/common/router';

import {AccessDeniedComponent} from '../pages/accessDenied/accessDenied.component';
import {NotFoundComponent} from '../pages/notFound/notFound.component';

export const components: Type<any>[] =
[
    AccessDeniedComponent,
    NotFoundComponent
];

export const routesOptions: ModuleRoutesOptions =
{
    rootModule: true,
    rootModuleConfig:
    {
        enableTracing: false,
        preloadingStrategy: PreloadAllModules
    },
    staticRoutesBefore:
    [
        //default page redirect
        {
            path: '',
            redirectTo: 'merania',
            pathMatch: 'full'
        },
        {
            path: '',
            loadChildren: () => import('../pages/+default/default.module').then(({DefaultModule}) => DefaultModule)
        },
        {
            path: 'samples',
            loadChildren: () => import('../pages/+samples/samples.module').then(({SamplesModule}) => SamplesModule)
        },
        {
            path: 'merania',
            loadChildren: () => import('../pages/+merania/merania.module').then(({MeraniaModule}) => MeraniaModule)
        },
        {
            path: 'pacienti',
            loadChildren: () => import('../pages/+pacienti/pacienti.module').then(({PacientiModule}) => PacientiModule)
        },
        {
            path: 'lekari',
            loadChildren: () => import('../pages/+lekari/lekari.module').then(({LekariModule}) => LekariModule)
        },
        {
            path: 'zariadenia',
            loadChildren: () => import('../pages/+zariadenia/zariadenia.module').then(({ZariadeniaModule}) => ZariadeniaModule)
        },
        {
            path: 'pomoc',
            loadChildren: () => import('../pages/+pomoc/pomoc.module').then(({PomocModule}) => PomocModule)
        }
    ]
};