/* eslint-disable ressurectit/imports-order */
import './dependencies';
import './dependencies.browser';
import 'zone.js/dist/zone';
import './hacks';
import {platformBrowser} from '@angular/platform-browser';
import {NgModuleRef, enableProdMode, StaticProvider, ValueProvider, PlatformRef} from '@angular/core';
import {runWhenModuleStable} from '@anglr/common';
import {RestTransferStateService} from '@anglr/rest';
import {hmrAccept, hmrFinishedNotification} from '@anglr/common/hmr';
import {LogEventLevel} from 'structured-log';

import {config} from './config';
import {BrowserAppModule} from './boot/browser-app.module';
import {NG_APP_STABLE} from './misc/tokens';
import {IS_ELECTRON} from './modules';

rendererLogger.log('Main browser is loading', LogEventLevel.verbose);

function start(providers: StaticProvider[])
{
    rendererLogger.log('Starting bootstrapping of angular', LogEventLevel.verbose);

    platform = platformBrowser(providers);

    runWhenModuleStable(platform.bootstrapModule(BrowserAppModule), (moduleRef: NgModuleRef<any>) => 
    {
        // moduleRef.injector.get(LoggerService).verbose('Angular finished bootstraping and is stable');
        moduleRef.injector.get(NG_APP_STABLE).isStable();

        moduleRef.injector.get(RestTransferStateService)?.clearAndDeactivate();
        jsDevMode && hmrFinishedNotification();
    }, config.configuration.debug);
}

if(isProduction)
{
    enableProdMode();
}

if (jsDevMode && module['hot'])
{
    module['hot'].accept();
}

jsDevMode && hmrAccept(() => platform);

let platform: PlatformRef;

//running in electron
if(isElectron)
{
    import('./staticProviders.electron').then(({providers}) =>
    {
        start(
        [
            ...providers,
            <ValueProvider>
            {
                provide: IS_ELECTRON,
                useValue: true
            }
        ]);
    });
}
//running in browser
else
{
    import('./staticProviders').then(({providers}) =>
    {
        const emulateElectron = jsDevMode && config.configuration.emulateElectron;

        start(
        [
            ...providers,
            <ValueProvider>
            {
                provide: IS_ELECTRON,
                useValue: emulateElectron
            }
        ]);
    });
}
