import {Inject, Injector, Pipe, PipeTransform} from '@angular/core';
import {GridColumn, GridComponent} from '@anglr/grid';

import {ColumnContext, ColumnContextGetter} from '../services';
import {COLUMN_CONTEXT_GETTER} from '../tokens';

/**
 * Gets column context for dynamic template
 */
@Pipe({name: 'getColumnContext'})
export class GetColumnContextPipe<TData = any, TColumn extends GridColumn = any, TContext extends ColumnContext = any> implements PipeTransform
{
    //######################### constructor #########################\\
    constructor(@Inject(COLUMN_CONTEXT_GETTER) private _columnContextGetter: ColumnContextGetter<TData, TColumn, TContext>,
                private _grid: GridComponent,
                private _injector: Injector)
    {
    }

    //######################### public methods - implementation of PipeTransform #########################

    /**
     * Gets column context
     * @param datum transformValueComment
     */
    public transform(datum: TData, meta: TColumn, index: number, startingIndex: number): TContext
    {
        return this._columnContextGetter.getColumnContext(this._grid, this._injector, datum, meta, index, startingIndex);
    }
}