import {Directive, Input, OnDestroy, OnInit, Output, EventEmitter} from '@angular/core';
import {BreakpointObserver} from '@angular/cdk/layout';
import {isNumber} from '@jscrpt/common';
import {Subscription} from 'rxjs';

import {RESPONSIVE_WIDTH} from '../../../../misc/constants';

/**
 * Directive used for testing and matching media query width
 */
@Directive(
{
    selector: '[maxWidthQuery]'
})
export class MaxWidthQueryDirective implements OnInit, OnDestroy
{
    //######################### private fields #########################
    
    /**
     * All initialization subscriptions
     */
    private _initSubscriptions: Subscription = new Subscription();

    /**
     * Gets value of current responsive treshold
     */
    private _value: number = RESPONSIVE_WIDTH;

    //######################### public properties - inputs #########################

    /**
     * Max width query value in pixels
     */
    @Input()
    public get maxWidthQuery(): number|string
    {
        return this._value;
    }
    public set maxWidthQuery(value: number|string)
    {
        if(isNumber(value))
        {
            this._value = value;
        }
    }

    //######################### public properties - outputs #########################

    /**
     * Occurs when there is change in 'match' also emitted on first initialization
     */
    @Output()
    public queryMatch: EventEmitter<boolean> = new EventEmitter<boolean>();
     
    //######################### constructor #########################
    constructor(private _breakPointObserver: BreakpointObserver)
    {
    }

    //######################### public methods - implementation of OnInit #########################
    
    /**
     * Initialize component
     */
    public ngOnInit()
    {
        this._initSubscriptions.add(this._breakPointObserver
                                        .observe(`(max-width: ${this._value}px)`)
                                        .subscribe(state => this.queryMatch.emit(state.matches)));
    }

    //######################### public methods - implementation of OnDestroy #########################
    
    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        this._initSubscriptions?.unsubscribe();
        this._initSubscriptions = null;
    }
}