import {Component, ChangeDetectionStrategy, Inject, OnInit, OnDestroy, ChangeDetectorRef} from '@angular/core';
import {fadeInOutTrigger} from '@anglr/animations';
import {Subscription} from 'rxjs';

import {TITLE_BAR} from '../../../../misc/tokens';
import {TitleBar} from '../../../../services/titleBar';

/**
 * Component for displaying window title bar
 */
@Component(
{
    selector: 'div.title-bar',
    templateUrl: 'titleBar.component.html',
    styleUrls: ['titleBar.component.scss'],
    animations: [fadeInOutTrigger],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class TitleBarComponent implements OnInit, OnDestroy
{
    //######################### private fields #########################

    /**
     * Subscription for changes of maximize state
     */
    private _maximizeChangeSubscription: Subscription;

    //######################### constructor #########################
    constructor(@Inject(TITLE_BAR) public titleBarSvc: TitleBar,
                private _changeDetector: ChangeDetectorRef)
    {
    }

    //######################### public methods - implementation of OnInit #########################
    
    /**
     * Initialize component
     */
    public ngOnInit()
    {
        this._maximizeChangeSubscription = this.titleBarSvc.maximizedChange.subscribe(() => this._changeDetector.detectChanges());
    }
    
    //######################### public methods - implementation of OnDestroy #########################
    
    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        this._maximizeChangeSubscription?.unsubscribe();
        this._maximizeChangeSubscription = null;
    }
}