/**
 * Creates APP initialization factory, that first try to authorize user before doing anything else
 * @param authService Authentication service used for authentication of user
 */
export function appInitializerFactory(authService: AuthenticationService<any>,
                                      keycloak: KeycloakService,
                                      storage: PermanentStorage,
                                      sessionService: SessionService): () => Promise<void>
{
    return async () =>
    {
        sessionService.startSessionTimeout(config.configuration.keycloak.maxInactiveIntervalInSec || 1800,
                                           config.configuration.keycloak.pingIntervalInSec || 45);

        const redirectUri = new RegExp('^' + config.configuration.keycloak.redirectUri).test(window.location.href) ? window.location.href : config.configuration.keycloak.redirectUri;

        let token = storage.get<string>(AUTH_TOKEN);

        await keycloak.init(
        {
            config:
            {
                url: config.configuration.keycloak.oauthServerUrl,
                realm: config.configuration.keycloak.keycloakRealm,
                clientId: config.configuration.keycloak.keycloakClient
            },
            initOptions:
            {
                redirectUri: redirectUri,
                onLoad: 'login-required',
                // enableLogging: true,
                checkLoginIframe: false,
                token: token
            },
            enableBearerInterceptor: true,
            loadUserProfileAtStartUp: true
        });

        token = await keycloak.getToken();
        storage.set(AUTH_TOKEN, token);

        try
        {
            await authService
                .getUserIdentity();
        }
        catch(e)
        {
            alert(`Authentication failed: ${e}`);

            throw e;
        }
    };
}

/**
 * Factory for HttpErrorInterceptorOptions
 */
export function httpErrorInterceptorOptionsFactory()
{
    return new HttpErrorInterceptorOptions(config.configuration.debug);
}

/**
 * Response mapping function
 */
export function httpErrorInterceptorMappingFunction(err: any) : BadRequestDetail
{
    const result =
    {
        errors: [],
        validationErrors: {}
    };

    if(err && err.message)
    {
        result.errors.push(err.message);
    }

    if(err && err.errors && Array.isArray(err.errors))
    {
        (<Array<any>>err.errors).forEach(itm =>
        {
            let message = '';

            if(itm.defaultMessage)
            {
                message += itm.defaultMessage;
            }

            if(itm.code)
            {
                message = `${itm.code}: ${message}`;
            }

            if(message)
            {
                result.errors.push(message);
            }
        });
    }

    return result;
}

/**
 * Factory method for creating HttpGatewayTimeoutInterceptorOptions
 */
export function httpGatewayTimeoutInterceptorOptionsFactory()
{
    return new HttpGatewayTimeoutInterceptorOptions('Server neodpovedal v stanovenom ase.');
}

/**
 * Factory method for creating NoConnectionInterceptorOptions
 */
export function noConnectionInterceptorOptionsFactory()
{
    return new NoConnectionInterceptorOptions('Server je mimo prevdzky.');
}

