import {Component, ChangeDetectionStrategy, Inject, Optional, ElementRef, ChangeDetectorRef, forwardRef, HostBinding, OnDestroy, OnInit} from '@angular/core';
import {BreakpointObserver} from '@angular/cdk/layout';
import {BasicTableColumn, BasicTableMetadata, ContentRendererAbstractComponent, CONTENT_RENDERER_OPTIONS, GridPluginInstances, GRID_PLUGIN_INSTANCES, PluginDescription} from '@anglr/grid';
import {extend} from '@jscrpt/common';
import {Subscription} from 'rxjs';

import {ResponsiveCssDivsContentRendererOptions} from './responsiveCssDivsContentRenderer.interface';
import {ResponsiveCssDivsBodyContentRendererComponent} from './body/responsiveCssDivsBodyContentRenderer.component';
import {ResponsiveCssDivsHeaderContentRendererComponent} from './header/responsiveCssDivsHeaderContentRenderer.component';
import {RESPONSIVE_WIDTH_MEDIA} from '../../../../../misc/constants';

/**
 * Default options for 'ResponsiveCssDivsContentRendererComponent'
 * @internal
 */
const defaultOptions: ResponsiveCssDivsContentRendererOptions =
{
    cssClasses:
    {
        containerDiv: 'css-grid-table'
    },
    plugins:
    {
        bodyRenderer: <PluginDescription<ResponsiveCssDivsBodyContentRendererComponent>>
        {
            type: forwardRef(() => ResponsiveCssDivsBodyContentRendererComponent)
        },
        headerRenderer: <PluginDescription<ResponsiveCssDivsHeaderContentRendererComponent>>
        {
            type: forwardRef(() => ResponsiveCssDivsHeaderContentRendererComponent)
        }
    }
};

/**
 * Component used for 'ResponsiveCssDivsContentRendererComponent'
 */
@Component(
{
    selector: 'div.css-grid-content-renderer',
    templateUrl: 'responsiveCssDivsContentRenderer.component.html',
    styleUrls: ['responsiveCssDivsContentRenderer.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResponsiveCssDivsContentRendererComponent<TOrdering = any, TData = any, TColumn extends BasicTableColumn = any, TMetadata extends BasicTableMetadata<TColumn> = any> extends ContentRendererAbstractComponent<TOrdering, TData, TMetadata, ResponsiveCssDivsContentRendererOptions> implements OnDestroy, OnInit
{
    //######################### protected fields #########################

    /**
     * Subscription for responsive changes
     */
     protected _responsiveSubscription: Subscription;

    //######################### public properties - template binding #########################

    /**
     * Indication whether is responsive view enabled
     */
     public responsiveView: boolean = false;

    /**
     * Array of columns currently visible
     */
    public get columns(): BasicTableColumn[]
    {
        return this._metadataSelector?.metadata.columns;
    }

    //######################### public properties - host bindings #########################


    /**
     * Css class applied to grid itself
     */
    @HostBinding('class')
    public get cssClass(): string
    {
        return this._options.cssClasses.containerDiv;
    }

    //######################### constructor #########################
    constructor(protected _breakpointObserver: BreakpointObserver,
                pluginElement: ElementRef,
                @Inject(GRID_PLUGIN_INSTANCES) @Optional() gridPlugins: GridPluginInstances,
                @Inject(CONTENT_RENDERER_OPTIONS) @Optional() options?: ResponsiveCssDivsContentRendererOptions,
                protected _changeDetector?: ChangeDetectorRef)
    {
        super(pluginElement, gridPlugins);

        this._options = extend(true, {}, defaultOptions, options);
    }

    //######################### public methods - implementation of OnInit #########################

    /**
     * Initialize component
     */
    public ngOnInit()
    {
        this._responsiveSubscription = this._breakpointObserver.observe(RESPONSIVE_WIDTH_MEDIA)
            .subscribe(({matches}) =>
            {
                this.responsiveView = matches;
                this._changeDetector.detectChanges();
            });
    }

    //######################### public methods - implementation of OnDestroy #########################
    
    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        super.ngOnDestroy();

        this._responsiveSubscription?.unsubscribe();
        this._responsiveSubscription = null;
    }

    //######################### protected methods #########################

    /**
     * Invalidates visuals, redraw template
     */
    protected _invalidateVisuals()
    {
        super._invalidateVisuals();

        this._changeDetector.detectChanges();
    }
}
