import {ClassProvider, Injectable} from '@angular/core';
import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HTTP_INTERCEPTORS} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {catchError, tap} from 'rxjs/operators';

import {SessionService} from './session.service';

/**
 * Interceptor used for refreshing session
 */
@Injectable()
export class SessionInterceptor implements HttpInterceptor
{
    //######################### private fields #########################
    
    /**
     * Url that is exception for refreshing session
     */
    private _inactivityRequestExceptions = [/\/ping$/];

    //######################### constructor #########################
    constructor(private _sessionService: SessionService)
    {
    }

    //######################### public methods #########################
    
    /**
     * Intercepts http request and refresh session of required
     */
    public intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>>
    {
        return next.handle(request).pipe(
            tap(data =>
            {
                if(data.type === 4)
                {
                    if (!this.isInRequestExceptions(request.url))
                    {
                        this._sessionService.updateSessionTimeout();
                    }
                    else
                    {
                        console.log(`Request URL (${request.url}) is on inactivity exception list, session timeout not changed`);
                    }
                }
            }),
            catchError(error =>
            {
                if (error.status === 401)
                {
                    this._sessionService.logout();
                }

                return throwError(error);
            }));
    }

    //######################### private methods #########################

    /**
     * Tests whether is url contain in array of exceptions
     */
    private isInRequestExceptions(url: string)
    {
        return this._inactivityRequestExceptions.some(exception => exception.test(url));
    }
}

/**
 * Session interceptor provider
 */
export const SESSION_INTERCEPTOR: ClassProvider =
{
    provide: HTTP_INTERCEPTORS,
    useClass: SessionInterceptor,
    multi: true
};