import {animate, style, transition, trigger} from '@angular/animations';

import {ANIMATE_TO_HIDDEN, ANIMATE_TO_VISIBLE} from '../../../../misc/constants';

export const menuSlideAnimation = trigger('menuSlide',
[
    transition(`${ANIMATE_TO_HIDDEN} => ${ANIMATE_TO_VISIBLE}`,
    [
        style(
        {
            height: 0,
            color: 'transparent'
        }),
        animate('260ms ease-in', style(
        {
            height: '*',
            color: '*'
        }))
    ]),
    transition(`${ANIMATE_TO_VISIBLE} => ${ANIMATE_TO_HIDDEN}`,
    [
        animate('260ms ease-out', style(
        {
            height: 0,
            color: 'transparent'
        }))
    ])
]);