import {Component, ChangeDetectionStrategy, HostBinding, Optional, OnDestroy, EventEmitter, Output, ChangeDetectorRef} from '@angular/core';
import {Subscription} from 'rxjs';

import {MaxWidthQueryDirective} from '../../directives';

/**
 * Component used for displaying three lines svg
 */
@Component(
{
    selector: 'div.three-lines',
    templateUrl: 'threeLinesIcon.component.html',
    styleUrls: ['threeLinesIcon.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ThreeLinesIconComponent implements OnDestroy
{
    //######################### private fields #########################

    /**
     * Subscription for change of media query match
     */
    private _queryChangeSubscription: Subscription;

    //######################### public properties - outputs #########################

    /**
     * Occurs when there is change in visibility of three lines 
     */
    @Output()
    public visibleChange: EventEmitter<boolean> = new EventEmitter<boolean>();

    //######################### public properties - host #########################

    /**
     * Indication whether is three lines icon hidden
     */
    @HostBinding('class.hidden')
    public hidden: boolean = false;
    
    //######################### constructor #########################
    constructor(changeDetector: ChangeDetectorRef,
                @Optional() private _maxWidthQuery?: MaxWidthQueryDirective)
    {
        if(this._maxWidthQuery)
        {
            this._queryChangeSubscription = this._maxWidthQuery.queryMatch.subscribe(value =>
            {
                this.hidden = !value;
                this.visibleChange.emit(!value);
                changeDetector.markForCheck();
            });
        }
        else
        {
            console.warn('Missing maxWidthQuery directive, automatic change of visible state will not work!');
        }
    }

    //######################### public methods - implementation of OnDestroy #########################
    
    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        this._queryChangeSubscription?.unsubscribe();
        this._queryChangeSubscription = null;
    }
}