import {AfterContentInit, ContentChildren, Directive, ElementRef, HostListener, OnDestroy, QueryList} from '@angular/core';
import {BreakpointObserver} from '@angular/cdk/layout';
import {ClickOutsideDirective} from '@anglr/common';
import {createPopper, Instance} from '@popperjs/core';
import {Subscription} from 'rxjs';

import {RESPONSIVE_WIDTH_MEDIA} from '../../../../misc/constants';

/**
 * Directive used for handling submenu popup
 */
@Directive(
{
    selector: 'a[submenuPopup]'
})
export class SubmenuPopupDirective implements AfterContentInit, OnDestroy
{
    //######################### private fields #########################

    /**
     * Subscriptions for click outside directive changes
     */
    private _clickOutsideSubscriptions: Subscription = new Subscription();

    /**
     * Indication whether popup is visible
     */
    private _popupVisible: boolean = false;

    /**
     * Html div popup element
     */
    private _divPopup: HTMLDivElement;

    /**
     * Span icon element
     */
    private _spanIcon: HTMLSpanElement;

    /**
     * Instance of popperjs
     */
    private _popperInstance: Instance;

    //######################### public properties - children #########################

    /**
     * Click outside directives
     */
    @ContentChildren(ClickOutsideDirective, {descendants: true})
    public clickOutsideElements: QueryList<ClickOutsideDirective>;

    //######################### constructor #########################
    constructor(private _element: ElementRef<HTMLAnchorElement>,
                private _breakPointObserver: BreakpointObserver)
    {
    }

    //######################### public methods - implementation of AfterContentInit #########################

    /**
     * Called when content was initialized
     */
    public ngAfterContentInit()
    {
        const element = this._element.nativeElement;

        this._divPopup = element.querySelector('div');
        this._spanIcon = element.querySelector('.fas');

        this.clickOutsideElements.toArray().forEach(itm => 
        {
            itm.clickOutsideElement = this._element.nativeElement;

            this._clickOutsideSubscriptions.add(itm.clickOutsideConditionChange.subscribe(() =>
            {
                if(this._popupVisible && !this._breakPointObserver.isMatched(RESPONSIVE_WIDTH_MEDIA))
                {
                    this._togglePopupVisibility();
                }
            }));
        });

        if(!this._divPopup || !this._spanIcon)
        {
            throw new Error('Missing popup div or icon span.fas!');
        }

        this._divPopup.remove();
    }

    //######################### public methods - implementation of OnDestroy #########################
    
    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        this._popperInstance?.destroy();
        this._popperInstance = null;
        
        this._clickOutsideSubscriptions?.unsubscribe();
        this._clickOutsideSubscriptions = null;
    }

    //######################### public methods - hosts #########################

    /**
     * Toggles popup visibility
     */
    @HostListener('click', ['$event'])
    public togglePopup(event: MouseEvent)
    {
        event.preventDefault();
        event.stopPropagation();

        this._togglePopupVisibility();
    }

    //######################### private methods #########################

    /**
     * Toggles visibility of popup
     */
    private _togglePopupVisibility()
    {
        this._popupVisible = !this._popupVisible;
        this._spanIcon.classList.toggle('fa-caret-down');
        this._spanIcon.classList.toggle('fa-caret-up');

        if(this._popupVisible)
        {
            this._element.nativeElement.append(this._divPopup);

            if(!this._breakPointObserver.isMatched(RESPONSIVE_WIDTH_MEDIA))
            {
                this._popperInstance?.destroy();
    
                this._popperInstance = createPopper(this._element.nativeElement, this._divPopup,
                {
                    placement: 'bottom-start'
                });
            }
        }
        else
        {
            this._divPopup.remove();
        }
    }
}