import {Pipe, PipeTransform} from '@angular/core';

/**
 * Drops empty items from array
 */
@Pipe({name: 'dropEmptyItems'})
export class DropEmptyItemsPipe<TItem> implements PipeTransform
{
    /**
     * Drops empty items from array
     * @param value Array to be checked
     */
    public transform(value: Array<TItem>): Array<TItem>
    {
        return value.filter(itm => !!itm);
    }
}