import {Injectable, Injector} from '@angular/core';
import {BasicTableColumn, BasicTableColumnContext, Grid} from '@anglr/grid';

import {ColumnContextGetter} from './columnContextGetter.interface';

/**
 * Service used for getting BasicTableColumnContext
 */
@Injectable()
export class BasicColumnContextGetterService<TData = any> implements ColumnContextGetter<TData, BasicTableColumn<TData>, BasicTableColumnContext<TData>>
{
    //######################### public methods - implementation of ColumnContextGetter #########################

    /**
     * Gets column context
     * @param grid - Instance of grid itself
     * @param injector - Injector available in grid
     * @param datum - Instance of row data
     * @param meta - Instance of column metadata
     * @param index - Index of rendered row in current page
     * @param startingIndex - Starting index of currently displayed items
     */
    public getColumnContext(_grid: Grid, _injector: Injector, datum: TData, meta: BasicTableColumn<TData>, index: number, startingIndex: number): BasicTableColumnContext<TData>
    {
        return new BasicTableColumnContext(datum, meta, index, startingIndex);
    }
}