import {Injectable} from '@angular/core';
import {AuthenticationService} from '@anglr/authentication';
import {TitledDialogService} from '@anglr/common/material';
import {isBlank} from '@jscrpt/common';
import {isBefore, subSeconds, addSeconds} from 'date-fns';

import {PingService} from '../api/ping';
import {InactivityDialogComponent} from '../../modules';

function plural(num: number): boolean
{
    return num > 1 && num < 5 && ~~(num / 10) !== 1;
}

// in seconds
const BEFORE_SESSION_END = 60;

/**
 * Service that is used for managing auth session
 */
@Injectable({providedIn: 'root'})
export class SessionService
{
    intervalId: number = null;
    maxInactiveIntervalInSec: number;
    sessionExpirationDate: Date = null;

    // it's the
    finalCountdownId: number = null;

    constructor(private _authSvc: AuthenticationService<any>,
                private _pingSvc: PingService,
                private _dialog: TitledDialogService)
    {
    }

    private setInactivityInterval()
    {
        this.intervalId = window.setInterval(async () =>
        {
            if (!isBefore(new Date(), subSeconds(this.sessionExpirationDate, BEFORE_SESSION_END)))
            {
                window.clearInterval(this.intervalId);
                this.setFinalCountdown();

                const minutes = Math.floor(BEFORE_SESSION_END / 60);
                const timeSpan = minutes > 1 ? minutes + ' ' + (plural(minutes) ? 'minty' : 'mint') : 'mintu';

                const result = await this._dialog.open<InactivityDialogComponent, string, boolean>(InactivityDialogComponent,
                {
                    title: 'Upozornenie na inaktivitu',
                    width: '40wv',
                    data: timeSpan
                }).afterClosed().toPromise();

                if(isBlank(result))
                {
                    return;
                }

                if (result)
                {
                    this._pingSvc.ping().subscribe(() =>
                    {
                        window.clearInterval(this.finalCountdownId);
                        this.updateSessionTimeout();
                        this.setInactivityInterval();
                    });
                }
                else
                {
                    window.clearInterval(this.finalCountdownId);
                    this.logout();
                }
            }
        }, 1000);
    }

    private startPinging(pingIntervalInSec)
    {
        window.setInterval(() =>
        {
            this._pingSvc.ping().subscribe(() => console.log('PING sent', Date.now()));
        }, pingIntervalInSec * 1000);
    }

    private setFinalCountdown()
    {
        this.finalCountdownId = window.setInterval(() =>
        {
            if (!isBefore(new Date(), this.sessionExpirationDate))
            {
                this.logout();
            }
        }, 1000);
    }

    startSessionTimeout(maxInactiveIntervalInSec: number, pingIntervalInSec: number)
    {
        this.maxInactiveIntervalInSec = maxInactiveIntervalInSec;
        this.sessionExpirationDate = addSeconds(new Date(), this.maxInactiveIntervalInSec);

        if (!this.intervalId)
        {
            this.setInactivityInterval();
            this.startPinging(pingIntervalInSec);
        }
        else
        {
            console.error('Error: session timeout already started');
        }
    }

    updateSessionTimeout(timeout?: number)
    {
        this.maxInactiveIntervalInSec = timeout || this.maxInactiveIntervalInSec;
        this.sessionExpirationDate = addSeconds(new Date(), this.maxInactiveIntervalInSec);
    }

    logout()
    {
        this._authSvc.logout().subscribe(() => {});
    }
}
