import {InjectionToken} from '@angular/core';

import {ColumnContextGetter, BasicColumnContextGetterService} from './services';

/**
 * Injection token used for getting column context getter
 */
export const COLUMN_CONTEXT_GETTER: InjectionToken<ColumnContextGetter> = new InjectionToken<ColumnContextGetter>('COLUMN_CONTEXT_GETTER', {providedIn: 'root', factory: () => new BasicColumnContextGetterService});