import {Component, ChangeDetectionStrategy, OnInit, OnDestroy, ChangeDetectorRef} from '@angular/core';
import {Router} from '@angular/router';
import {AuthenticationService, UserIdentity} from '@anglr/authentication';
import {TitledDialogService} from '@anglr/common/material';
import {Subscription} from 'rxjs';

import {UserSettingsComponent} from '../../../userSettings';
import {ANIMATE_TO_HIDDEN, ANIMATE_TO_VISIBLE} from '../../../../misc/constants';
import {menuSlideAnimation} from './mainMenu.animations';

/**
 * Component used for displaying application main menu
 */
@Component(
{
    selector: 'main-menu',
    templateUrl: 'mainMenu.component.html',
    styleUrls: ['mainMenu.component.scss'],
    animations: [menuSlideAnimation],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class MainMenuComponent implements OnInit, OnDestroy
{
    //######################### private fields #########################

    /**
     * Subscription created during init
     */
    private _initSubscriptions: Subscription = new Subscription();

    //######################### public properties - template bindings #########################

    /**
     * Name of currently logged user
     */
    public loggedUser: string = '';

    /**
     * Indication whether is menu visible
     */
    public menuVisible: boolean = true;

    /**
     * Name of animation 'state' for slide in out animation of menu
     */
    public menuAnimation: string = ANIMATE_TO_HIDDEN;

    //######################### constructor #########################
    constructor(private _authSvc: AuthenticationService<any>,
                private _router: Router,
                private _dialog: TitledDialogService,
                private _changeDetector: ChangeDetectorRef)
    {
    }

    //######################### public methods - implementation of OnInit #########################

    /**
     * Initialize component
     */
    public ngOnInit()
    {
        const buildLoggedUser = (identity: UserIdentity) =>
        {
            this.loggedUser = `${identity.firstName} ${identity.surname}`;
        };

        buildLoggedUser(this._authSvc.userIdentity);

        this._initSubscriptions.add(this._authSvc.authenticationChanged.subscribe(identity =>
        {
            if(identity.isAuthenticated)
            {
                buildLoggedUser(identity);
            }
            else
            {
                this.loggedUser = '';
            }
        }));
    }

    //######################### public methods - implementation of OnDestroy #########################

    /**
     * Called when component is destroyed
     */
    public ngOnDestroy()
    {
        this._initSubscriptions?.unsubscribe();
        this._initSubscriptions = null;
    }

    //######################### public methods - template bindings #########################

    /**
     * Changes animation state to hidden
     */
    public animationToHidden()
    {
        this.menuAnimation = ANIMATE_TO_HIDDEN;
    }

    /**
     * Updates animation
     */
    public updateAnimation()
    {
        const toggleVisibility = () =>
        {
            this.menuVisible = !this.menuVisible;

            this._changeDetector.detectChanges();
        };

        const toggleAnimation = () =>
        {
            if(this.menuAnimation == ANIMATE_TO_HIDDEN)
            {
                this.menuAnimation = ANIMATE_TO_VISIBLE;
            }
            else
            {
                this.menuAnimation = ANIMATE_TO_HIDDEN;
            }

            this._changeDetector.detectChanges();
        };

        //show
        if(!this.menuVisible)
        {
            toggleVisibility();
            toggleAnimation();
        }
        //hide
        else
        {
            toggleAnimation();

            //TODO - solve this differently using pure animation
            setTimeout(() =>
            {
                toggleVisibility();
            }, 260);
        }
    }

    /**
     * Logs out user
     */
    public async logout()
    {
        this._authSvc
            .logout()
            .subscribe(() =>
            {
                this._router.navigate(['/login']);
            });
    }

    /**
     * Opens settings dialog
     */
    public openSettings()
    {
        this._dialog.open(UserSettingsComponent,
        {
            title: 'user settings',
            maxHeight: '80vh'
        });
    }
}